import React from 'react';
const App=()=>{
  return (
      <section className="counter">
          <div className="counter-overlay">
              <div className="container wow bounceInLeft" data-wow-duration="1s">
                  <div className="row text-center">
                      <div className="col-md-3">
                          <div className="counter-item">
                              <div><i className="fa fa-cloud-download" /></div>
                              <h2><span className="counter-num">1588 </span><span>+</span></h2>
                              <p>Download</p>
                          </div>
                      </div>
                      <div className="col-md-3">
                          <div className="counter-item">
                              <div><i className="fa fa-gift" /></div>
                              <h2><span className="counter-num">3500 </span><span>+</span></h2>
                              <p>Partners</p>
                          </div>
                      </div>
                      <div className="col-md-3">
                          <div className="counter-item">
                              <div><i className="fa fa-heart-o" /></div>
                              <h2><span className="counter-num">199 </span><span>+</span></h2>
                              <p>Likes</p>
                          </div>
                      </div>
                      <div className="col-md-3">
                          <div className="counter-item">
                              <div><i className="fa fa-check" /></div>
                              <h2><span className="counter-num">1866 </span><span>+</span></h2>
                              <p>Recomended</p>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </section>

  );
}

export default App;
