import React from 'react';
const App = () => {
    return (
        <section className="contact-us" id="contact-us">
            <div className="comtainer bounceIn">
                <div className="row">
                    <div className="col-md-10 offset-md-1">
                        <div className="section-title">
                            <h2>contact us</h2>
                            <p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Corrupti mollitia dolorem culpa, veniam iusto hic atque quod vel possimus voluptates fugiat suscipit quo tempore, ipsum enim eos accusamus nulla aspernatur.</p>
                        </div>
                    </div>
                </div>
            </div>
            <div className="contact-us-form wow bounceIn">
                <div className="container">
                    <form role="form">
                        <div className="row">
                            <div className="col-md-4">
                                <input className="form-control" type="text" id="name" placeholder="Name" />
                                <input className="form-control" type="text" id="email" placeholder="Email" />
                                <input className="form-control" type="text" id="subject" placeholder="Subect" />
                            </div>
                            <div className="col-md-8">
                                <textarea className="form-control" id="message" cols={10} rows={25} placeholder="Message Text..." defaultValue={""} />
                                <button type="submit" className="btn btn-default submit-btn form_submit">SEND</button>
                            </div>
                        </div>
                    </form>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-md-12 wow bounceInLeft">
                            <div className="social-icons">
                                <ul>
                                    <li><a href="#"><i className="fa fa-facebook" /></a></li>
                                    <li><a href="#"><i className="fa fa-twitter" /></a></li>
                                    <li><a href="#"><i className="fa fa-youtube-play" /></a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

    );
}

export default App;
