import React from 'react';

const App = () => {
    return (
        <section className="testimonial">
            <div className="testimonial-overlay">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12 wow bounceInDown">
                            <div id="carousel-testimonial" className="carousel slide" data-ride="carousel">
                                <ol className="carousel-indicators">
                                    <li data-target="#carousel-testimonial" data-slide-to={0} className="active" />
                                    <li data-target="#carousel-testimonial" data-slide-to={1} />
                                </ol>
                                <div className="carousel-inner">
                                    <div className="carousel-item active text-center">
                                        <img src="https://www.qukads.com/images/na-testimony.png" className="center-block" alt="client-01" />
                                        <div className="testimonial-caption">
                                            <h2>Sanjana Reddy</h2>
                                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Itaque dolores repellat esse, quis voluptas vitae incidunt ipsa reiciendis cupiditate facilis expedita assumenda dignissimos, maiores illum. Dolorum velit recusandae asperiores fugiat.</p>
                                        </div>
                                    </div>
                                    <div className="carousel-item text-center">
                                        <img src="https://www.qukads.com/images/na-testimony.png" className="center-block" alt="client-02" />
                                        <div className="testimonial-caption">
                                            <h2>Ravi Singh</h2>
                                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Itaque dolores repellat esse, quis voluptas vitae incidunt ipsa reiciendis cupiditate facilis expedita assumenda dignissimos, maiores illum. Dolorum velit recusandae asperiores fugiat.</p>
                                        </div>
                                    </div>
                                </div>
                                <a className="carousel-control-prev" href="#carousel-testimonial" role="button" data-slide="prev">
                                    <span className="carousel-control-prev-icon" aria-hidden="true" />
                                    <span className="sr-only">Previous</span>
                                </a>
                                <a className="carousel-control-next" href="#carousel-testimonial" role="button" data-slide="next">
                                    <span className="carousel-control-next-icon" aria-hidden="true" />
                                    <span className="sr-only">Next</span>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

    );
}

export default App;
